import React, { Component } from 'react';
import logo from '../../logo.svg';
import '../../css/styles.css';
import icons from '../../vendors/feather/feather-sprite.svg';
import {
  Route,
  Link,
  Switch,
  HashRouter
} from "react-router-dom";
import Photography from "./Photography";
import Graphics from "./Graphics";
import Colors from "./Colors";
import Main from "./Main";
import createHashHistory from 'history/createHashHistory';
const hashHistory = createHashHistory({ basename: process.env.PUBLIC_URL });


class Sidebar extends Component {
  constructor(props) {
    super(props);
  }
  render() {
    return (
      <div className='sidebar'>
        
      </div>
    );
  }
}

const Logo = (props) => {
  return (
    <svg className={`header__logo feather feather-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="45px"
    height="45px"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${icons}#${props.name}`} />
    </svg>
  );
}

function Header() {
    return (
      <header className='header'>
        <h1 className="header__title"><Logo name='bookmark' />Makerbook</h1>
      </header>
    );
}



function TileIcon(props) {
  return (
    <svg className={`tile__icon feather feather-${props.name}`}
    xmlns= "http://www.w3.org/2000/svg"
    xmlnsXlink="http://www.w3.org/1999/xlink"
    viewBox='0 0 24 24'
    width="24"
    height="24"
    fill="none"
    stroke="currentColor"
    strokeWidth="2"
    strokeLinecap="round"
    strokeLinejoin="round">
      <use xlinkHref={`${icons}#${props.name}`} />
    </svg>
  );
}

class NavTile extends Component {
  constructor(props) {
    super(props);
  }
  
  render() {
    return (
      <div className='tile nav__tile'>
        <Link className='tile__link' to={this.props.tileURL}>
          <TileIcon className="tile__icon" name={this.props.tileIcon} />
          <span className='tile__name'>{this.props.tileName}</span>
        </Link>
      </div>
    );
  }
}

const Home = () => (
  <section className='section section--home'>
    <NavGrid />
  </section>
);

class NavGrid extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: ''
    }
  }
  
  render() {
    return (
      <div className ="grid">
      <NavTile tileName='Photography' tileURL='/photography' tileIcon='camera' />
      <NavTile tileName='Graphics' tileURL='/graphics' tileIcon='image' />
      <NavTile tileName='Textures' tileURL='' tileIcon='grid' />
      <NavTile tileName='Typography' tileURL='' tileIcon='type' />
      <NavTile tileName='Colors' tileURL='/colors' tileIcon='droplet' />
      <NavTile tileName='UI/UX' tileURL='' tileIcon='user' />
        </div>
    )
  }
}

class App extends Component {
  render() {
    const {location} = this.props;
    return (
      <HashRouter history={hashHistory}>
      <div className="app">
        <Header />
        <Switch location ={location}>
        <Route exact path="/" component={Home} />
          <Route path="/photography" component={Photography} />
          <Route path="/graphics" component={Graphics} />
          <Route path="/colors" component={Colors} />
        </Switch>
        </div>
      </HashRouter>
      )
  }
}

export default App;




